
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState, mapGetters } from "vuex";

const list = () => ["入党申请", "积极分子", "预备党员"];
export default {
components: {
XHeader,
Tab,
TabItem
},
computed: {
...mapGetters(["developPopup", "applyNum"])
},
data() {
return {
list: list(),
index: 0,
selected: "",
applys: [{ num: 0 }, { num: 0 }, { num: 0 }]
};
},
watch: {
applyNum: function(val) {
this.applys = val;
},
$route(to, from) {
if (to.query.status == "apply") {
this.selected = "入党申请";
}
}
},
created() {
let currentSelected = this.$route.query.status;
if (currentSelected === "apply") {
this.selected = "入党申请";
} else if (currentSelected === "positive") {
this.selected = "积极分子";
} else if (currentSelected === "prep") {
this.selected = "预备党员";
}
},
activated() {
let userid = window.localStorage.getItem("user");
this.$store.dispatch("getAppLyNum", { user_id: userid });
},
methods: {
handler(item) {
this.selected = item;
let path = this.$route.path;
if (item === "入党申请") {
this.$router.replace(`${path}?status=apply`);
} else if (item === "积极分子") {
this.$router.replace(`${path}?status=positive`);
} else if (item === "预备党员") {
this.$router.replace(`${path}?status=prep`);
}
},
changChoose() {
this.$store.dispatch("updateDevelopPopup", {
developPopup: true
});
}
}
};



